
#app .nav-wrapper {
  position: fixed;
  z-index: 100;
  top: 0;
  width: 100%;
}
#app .empty {
  height: 56px;
}
.switch-enter-active,
.switch-leave-active {
  transition: all 0.5s;
}
.switch-enter {
  position: absolute;
  top: 56pxs;
  transform: translate(100%, 0);
}
.switch-leave-active {
  position: absolute;
  top: 56px;
  transform: translate(-100%, 0);
}


