
.banner {
  height: 250px;
}
.banner .container {
  position: relative;
  width: 1200px;
  max-width: 1200px;
  height: 240px;
  margin: 10px auto 0;
}
.banner .container .box1,
.banner .container .box2,
.banner .container .box3,
.banner .container .box4,
.banner .container .box5 {
  position: absolute;
  display: block;
  background: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 5px 0 rgba(0,0,0,0.26);
  overflow: hidden;
  transition: all 1s ease;
}
.banner .container .box1 img,
.banner .container .box2 img,
.banner .container .box3 img,
.banner .container .box4 img,
.banner .container .box5 img {
  width: 100%;
  height: 100%;
  border-radius: 2px;
}
.banner .container .box1 {
  z-index: 1;
  top: 45px;
  left: 0px;
  width: 300px;
  height: 160px;
}
.banner .container .box2 {
  z-index: 3;
  top: 25px;
  left: 180px;
  width: 360px;
  height: 200px;
}
.banner .container .box3 {
  z-index: 5;
  top: 10px;
  left: 400px;
  width: 400px;
  height: 230px;
}
.banner .container .box4 {
  z-index: 3;
  top: 25px;
  left: 670px;
  width: 360px;
  height: 200px;
}
.banner .container .box5 {
  z-index: 1;
  top: 45px;
  left: 900px;
  width: 300px;
  height: 160px;
}
.banner .container .pre1,
.banner .container .pre2,
.banner .container .next1,
.banner .container .next2 {
  position: absolute;
  border-radius: 2px;
  cursor: pointer;
  background: #000;
  opacity: 0;
}
.banner .container .pre1 {
  z-index: 2;
  top: 45px;
  left: 0px;
  width: 300px;
  height: 160px;
}
.banner .container .pre2 {
  z-index: 4;
  top: 25px;
  left: 180px;
  width: 360px;
  height: 200px;
}
.banner .container .next1 {
  z-index: 4;
  top: 25px;
  left: 670px;
  width: 360px;
  height: 200px;
}
.banner .container .next2 {
  z-index: 2;
  top: 45px;
  left: 900px;
  width: 300px;
  height: 160px;
}
.banner .item-list {
  font-size: 0;
  text-align: center;
}
.banner .item-list .item {
  display: inline-block;
  width: 40px;
  height: 4px;
  margin: 5px 5px 0;
  background: rgba(255,255,255,0.6);
}
.banner .item-list .item.active {
  background: #fff;
}


