
.nav-item-wrapper {
  display: inline-block;
}
.nav-item-wrapper.router-link-active .nav-item {
  color: #fff;
}
.nav-item-wrapper .nav-item {
  position: relative;
  display: inline-block;
  width: 100px;
  height: 56px;
  line-height: 56px;
  font-size: 20px;
  color: rgba(255,255,255,0.6);
  text-align: center;
  cursor: pointer;
}
.nav-item-wrapper .nav-item.active {
  color: #fff;
}
.nav-item-wrapper .nav-item:hover {
  color: #fff;
}
.nav-item-wrapper .nav-item .fade-enter-active,
.nav-item-wrapper .nav-item .fade-leave-active {
  transition: all 0.5s ease;
  transition-delay: 0.1s;
}
.nav-item-wrapper .nav-item .fade-enter,
.nav-item-wrapper .nav-item .fade-leave-active {
  transform: translate(0, 14px);
  opacity: 0;
}
.nav-item-wrapper .nav-item .nav-sub-list {
  position: absolute;
  z-index: 5;
  top: 50px;
  left: 5px;
  width: 90px;
  background: #fff;
  border-radius: 2px;
  box-shadow: 0 0 5px 0 rgba(0,0,0,0.26);
}
.nav-item-wrapper .nav-item .nav-sub-list:before {
  position: absolute;
  z-index: 6;
  top: -5px;
  left: 40px;
  display: block;
  content: '';
  width: 0;
  height: 0;
  border-top: 0 dashed transparent;
  border-right: 5px dashed transparent;
  border-bottom: 5px solid #fff;
  border-left: 5px dashed transparent;
}
.nav-item-wrapper .nav-item .nav-sub-list:after {
  position: absolute;
  z-index: 5;
  top: -6px;
  left: 40px;
  display: block;
  content: '';
  width: 0;
  height: 0;
  border-top: 0 dashed transparent;
  border-right: 5px dashed transparent;
  border-bottom: 5px solid rgba(0,0,0,0.26);
  border-left: 5px dashed transparent;
}
.nav-item-wrapper .nav-item .nav-sub-list .nav-sub-item {
  display: block;
  height: 30px;
  line-height: 30px;
  font-size: 18px;
  font-weight: 500;
  color: #7e8c8d;
}
.nav-item-wrapper .nav-item .nav-sub-list .nav-sub-item:hover {
  color: #4285f4;
}


