
.loading {
  width: 50px;
  height: 25px;
  font-size: 0;
}
.loading .lt,
.loading .gt {
  position: relative;
  display: inline-block;
  width: 15px;
  height: 25px;
}
.loading .lt {
  animation: loading1 0.5s ease infinite alternate;
}
.loading .lt .line-top {
  position: absolute;
  top: 32%;
  display: inline-block;
  width: 15px;
  height: 2.5px;
  background: #4285f4;
  border-radius: 5px;
  transform: rotate(-30deg);
}
.loading .lt .line-bottom {
  position: absolute;
  bottom: 32%;
  display: inline-block;
  width: 15px;
  height: 2.5px;
  background: #4285f4;
  border-radius: 5px;
  transform: rotate(30deg);
}
.loading .gt {
  animation: loading3 0.5s ease infinite alternate;
}
.loading .gt .line-top {
  position: absolute;
  top: 32%;
  display: inline-block;
  width: 15px;
  height: 2.5px;
  background: #4285f4;
  border-radius: 5px;
  transform: rotate(30deg);
}
.loading .gt .line-bottom {
  position: absolute;
  bottom: 32%;
  display: inline-block;
  width: 15px;
  height: 2.5px;
  background: #4285f4;
  border-radius: 5px;
  transform: rotate(-30deg);
}
.loading .slash {
  display: inline-block;
  width: 15px;
  height: 25px;
  text-align: center;
}
.loading .slash .line {
  display: inline-block;
  width: 3px;
  height: 25px;
  background: #4285f4;
  border-radius: 5px;
  transform: rotate(30deg);
  animation: loading2 1s ease infinite;
}
@keyframes loading1 {
0% {
    transform: translate(0, 0);
}
100% {
    transform: translate(-10px, 0);
}
}
@keyframes loading2 {
0% {
    transform: rotate(30deg);
}
100% {
    transform: rotate(390deg);
}
}
@keyframes loading3 {
0% {
    transform: translate(0, 0);
}
100% {
    transform: translate(10px, 0);
}
}


