
.backtotop {
  width: 40px;
  height: 40px;
  text-align: center;
  color: #4285f4;
  background: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 5px 0 rgba(0,0,0,0.26);
  cursor: pointer;
  transition: all 0.5s ease;
}
.backtotop:hover {
  color: #fff;
  background: #4285f4;
}
.backtotop i {
  line-height: 40px;
  font-size: 20px;
}
.backfade-enter-active,
.backfade-leave-active {
  transition: all 0.5;
}
.backfade-enter,
.backfade-leave-active {
  opacity: 0;
}


