
.mediabanner {
  position: relative;
  width: 100%;
  height: 380px;
  border-radius: 2px;
  box-shadow: 0 2px 5px 0 rgba(0,0,0,0.26);
  overflow: hidden;
}
.mediabanner .media-banner-list {
  position: absolute;
  left: 0;
  width: 500%;
  height: 380px;
  font-size: 0;
  transition: all 1s ease;
}
.mediabanner .media-banner-list .media-banner-item {
  display: inline-block;
  width: 20%;
}
.mediabanner .media-banner-list .media-banner-item a {
  display: inline-block;
  width: 100%;
}
.mediabanner .media-banner-list .media-banner-item a img {
  width: 100%;
  height: 380px;
}
@media (max-width: 667px) {
.mediabanner .media-banner-list .media-banner-item a img {
    height: 200px;
}
}
@media (max-width: 667px) {
.mediabanner .media-banner-list {
    height: 200px;
}
}
.mediabanner .item-list {
  position: absolute;
  width: 100%;
  bottom: 0;
  text-align: center;
  font-size: 0;
}
.mediabanner .item-list .item {
  display: inline-block;
  width: 10px;
  height: 10px;
  margin: 10px 5px;
  border: 2px solid #4285f4;
  border-radius: 50%;
}
.mediabanner .item-list .item.active {
  background: #4285f4;
}
@media (max-width: 667px) {
.mediabanner {
    height: 200px;
}
}


