
.home {
  width: 100%;
}
@media (max-width: 1200px) {
.home .banner-wrapper {
    display: none;
}
}
.home .media-banner-wrapper {
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
}
@media (min-width: 1200px) {
.home .media-banner-wrapper {
    display: none;
}
}
.home .container {
  width: 1200px;
  max-width: 1200px;
  margin: 0 auto;
  font-size: 0;
}
.home .container .main-wrapper {
  display: inline-block;
  width: 800px;
}
.home .container .main-wrapper .profile-wrapper {
  display: block;
  margin: 10px 0;
}
.home .container .main-wrapper .load-article {
  width: 100%;
  height: 25px;
  margin: 10px 0;
  padding: 5px 0;
  text-align: center;
  line-height: 25px;
  font-size: 20px;
  color: #4285f4;
  background: #fff;
  box-shadow: 0 2px 5px 0 rgba(0,0,0,0.26);
  border-radius: 2px;
}
.home .container .main-wrapper .load-article .loading {
  text-align: justify;
  margin: 0 auto;
}
.home .container .main-wrapper .loading-wrapper {
  margin: 10px 0;
  padding: 5px 0;
  background: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 5px 0 rgba(0,0,0,0.26);
  cursor: pointer;
}
.home .container .main-wrapper .loading-wrapper .more {
  margin: 0 auto;
  line-height: 25px;
  font-size: 20px;
  text-align: center;
  color: #4285f4;
}
.home .container .main-wrapper .loading-wrapper .loading {
  margin: 0 auto;
}
.home .container .main-wrapper .init-enter-active,
.home .container .main-wrapper .init-leave-active {
  transition: all 0.5s ease;
}
.home .container .main-wrapper .init-enter {
  transform: translate(0, 30px);
  opacity: 0;
}
.home .container .main-wrapper .init-leave-active {
  display: none;
}
@media (max-width: 1200px) {
.home .container .main-wrapper {
    width: 100%;
}
}
.home .container .sidebar-wrapper {
  display: inline-block;
  vertical-align: top;
  width: 380px;
  margin-left: 20px;
}
.home .container .sidebar-wrapper.fixed {
  position: fixed;
  top: 56px;
}
.home .container .sidebar-wrapper .sidebar {
  margin: 10px 0;
}
@media (max-width: 1200px) {
.home .container .sidebar-wrapper {
    display: none;
}
}
@media (max-width: 1200px) {
.home .container {
    width: 100%;
    padding: 0 10px;
    box-sizing: border-box;
}
}
.home .backtotop-wrapper {
  position: fixed;
  left: 50%;
  bottom: 50px;
  margin-left: 220px;
}
@media (max-width: 1200px) {
.home .backtotop-wrapper {
    right: 20px;
    left: auto;
    opacity: 0.8;
}
}


