
.nav {
  position: relative;
  width: 100%;
  height: 56px;
  padding: 0 50px;
  background: #4285f4;
  box-shadow: 0 0 5px 0px #000;
  box-sizing: border-box;
}
.nav .logo-wrapper {
  float: left;
  font-size: 0;
}
.nav .logo-wrapper img {
  display: inline-block;
  margin: 3px 10px 3px 0px;
}
.nav .logo-wrapper .text {
  line-height: 56px;
  vertical-align: top;
  font-size: 20px;
  font-weight: 500;
  color: #fff;
}
.nav .nav-btn {
  float: right;
  width: 30px;
  height: 30px;
  margin: 13px 0;
  text-align: center;
  line-height: 30px;
  font-size: 20px;
  color: #fff;
  border: 1px solid #fff;
  border-radius: 2px;
}
@media (min-width: 1200px) {
.nav .nav-btn {
    display: none;
}
}
.nav .nav-list {
  float: right;
}
@media (max-width: 1200px) {
.nav .nav-list {
    display: none;
}
}
.nav .media-nav-list {
  display: none;
  position: absolute;
  z-index: -1;
  left: 0;
  bottom: -175px;
  width: 100%;
  padding: 0 10px;
  box-sizing: border-box;
}
.nav .media-nav-list .media-nav-item {
  width: 100%;
  height: 30px;
  margin: 5px auto;
  text-align: center;
  line-height: 30px;
  color: #fff;
  background: #4285f4;
  border-radius: 2px;
  box-shadow: 0 2px 5px 0 rgba(0,0,0,0.26);
}
.nav .media-nav-list .searchbar {
  margin: 0;
  height: 30px;
}
@media (max-width: 1200px) {
.nav .media-nav-list {
    display: block;
}
}
.nav .pull-enter-active,
.nav .pull-leave-active {
  transition: all 0.5s ease;
}
.nav .pull-enter,
.nav .pull-leave-active {
  transform: translate(0, -175px);
}
@media (max-width: 677px) {
.nav {
    padding: 0 10px;
}
}


