
.sidebar {
  width: 360px;
  max-width: 360px;
  padding: 10px;
  background: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 5px 0 rgba(0,0,0,0.26);
}
.sidebar .title {
  line-height: 40px;
  font-size: 20px;
  font-weight: 600;
  text-align: center;
  color: #4285f4;
}
.sidebar .tag {
  margin-right: 10px;
}
.sidebar .tag:last-child {
  margin: 0;
}


